<template>
  <div class="textareabox">
    <v-menu offset-y
            v-model="isShow"
            :close-on-content-click='false'>
      <template v-slot:activator="{ on, attrs }">
        <div>
          <v-textarea v-if="!isShow"
                      v-model="defaultcontent"
                      name="input-7-4"
                      v-bind="attrs"
                      v-on="on"
                      :style="{'background-color':!readonly?'rgb(242, 242, 242)':'#fff'}"
                      :readonly='true'
                      :label="label || $t('label_The_mail_text')"
                      append-icon="mdi-longitude"></v-textarea>
          <h4 v-else
              :style="{color: $vuetify.theme.defaults.light.primary}">{{label || $t('label_The_mail_text')}}</h4>
        </div>
      </template>
      <v-list class="bigbox">
        <v-list-item v-for="(item, index) in childrencontent"
                     :key="index">
          <v-textarea v-model="item.content"
                      @blur="handleinputBlur"
                      :readonly='readonly'
                      :rules='rules'
                      @change="change"
                      name="input-7-4"
                      :label="item.language_code+'*'"></v-textarea>
          <propertyDialog :readonly='!readonly'
                          :function_uuid='function_uuid'
                          :updateFunction_uuid='updateFunction_uuid'
                          @getpropertylist='getpropertylist($event,index)'></propertyDialog>
        </v-list-item>
        <div v-show="!readonly"
             class="btnbox">
          <mrc_ess_submitBtn @cancel='cancel'
                             @success='success'
                             cancelText='cancel'
                             successText='OK'></mrc_ess_submitBtn>
        </div>
      </v-list>

    </v-menu>

    <!-- <v-textarea v-if="!isShow"
                v-model="defaultcontent"
                name="input-7-4"
                :readonly='true'
                @click='changeisShow'
                :label="label"
                append-icon="mdi-longitude"></v-textarea>
    <h4 v-else
        style="color: #0F46C1">{{label}}</h4>

    <v-card :style="{display:isShow?'block':'none'}"
            style="box-shadow: -10px 10px 3px #eeeef0, 10px 10px 3px #eeeef0;">
      <v-card-text>
        <div v-for='(item,index) in childrencontent'
             :key="index">
          <v-textarea v-model="item.content"
                      @blur="handleinputBlur"
                      :readonly='readonly'
                      @change="change"
                      name="input-7-4"
                      :label="$t(`label.${item.language_code}`)"></v-textarea>
          <propertyDialog :readonly='!readonly'
                          position='realtive'
                          @getpropertylist='getpropertylist($event,index)'></propertyDialog>
        </div>

      </v-card-text>
      <v-card-actions>
        <mrc_ess_submitBtn @cancel='cancel'
                           cancelText='cancel'
                           successText='OK'
                           @success='success'></mrc_ess_submitBtn>
      </v-card-actions>
    </v-card> -->
  </div>
</template> 

<script>
import propertyDialog from './propertyDialog'
export default {
  components: {
    propertyDialog
  },
  props: {
    label: {
      type: String
    },
    defaultcontent: {
      type: String,
      default: '请设置内容'
    },
    content: {  //国际化字段
      type: Array,
      default: () => []
    },
    readonly: {
      type: Boolean,
      default: false
    },
    function_uuid: {
      type: String
    },
    updateFunction_uuid: {
      type: String
    },
  },
  model: {
    prop: 'content',
    event: 'contentChange'
  },
  data () {
    return {
      rules: [
        value => !!value || 'Required.',
      ],
      showMenu: false,
      childrencontent: [],
      blurIndex: '',
      isShow: false
    };
  },
  computed: {},
  created () { },
  watch: {
    content: {
      handler (v) {
        this.childrencontent = this.$clone(v)
      }, deep: true
    }
  },
  mounted () {
    this.childrencontent = this.$clone(this.content)
  },

  methods: {
    change (val) {
      // console.log(val);
    },
    success () {
      this.isShow = false
      this.$emit('contentChange', this.childrencontent)
    },
    cancel () {
      this.isShow = false
      this.childrencontent = this.$clone(this.content)
    },
    getpropertylist (v, index) {
      v.map(item1 => {
        this.childrencontent[index].content ? this.childrencontent[index].content = this.childrencontent[index].content.slice(0, this.blurIndex) + item1.property + this.childrencontent[index].content.slice(this.blurIndex) : this.childrencontent[index].content = item1.property
      })
      // this.$set(this.childrencontent, index, item)
    },
    handleinputBlur (e) {
      this.blurIndex = e.srcElement.selectionStart
      // let dd = e.srcElement.selectionEnd
      // console.log(this.blurIndex, dd);
    }
  },
};
</script>

<style scoped>
.bigbox {
  padding: 0;
}
.btnbox /deep/ .btnGroup {
  justify-content: initial;
  flex-direction: row-reverse;
  padding-right: 35px;
}
.bigbox /deep/ .v-list-item {
  display: block;
}
.bigbox /deep/ .v-.v-messages {
  min-height: 0px;
}
</style>
